  export default {
    name: 'HelloWorld',
    data() {
      return {
      info: null
      };
    },
    mounted() {
      axios
        .get('http://127.0.0.1:8000/api/v1/womendetail/1')
        .then(response => (this.info = response.data.photo));
        
      },
    props: {
      msg: String
    }
  }
  </script>
  
